/**
 * Show More Button & Rows
 */

//  This is useful since GHL's built-in show/hide section feature on buttons requires
//  the element to be shown is already hidden in the editor. Hidden elements are hard
//  to edit since they need to be manually unhidden in the layers panel, edited, and
//  then hidden again.
//
//  To use this Show More feature, add a .show-more class to a trigger button and then
//  add the .show-more class to one or more rows beneath the button. On load, the rows
//  will be auto hidden until the button is clicked.

const SEL_BTN_MORE = ".c-button .show-more-btn"
const SEL_BTN_LESS = ".c-button .show-less-btn"
const SEL_ROWS = ".c-row.show-more"
// Class added to rows after show more buttin is clicked
const SHOWN_CLASS = "show-more-shown"
// Class added to elements when show more is disabled
const DISABLED_CLASS = "show-more-disabled"

type Listener = (this: HTMLElement, event: Event) => void

const addClickListeners = (btnRow: Element, listener: Listener) => {
  btnRow.querySelectorAll(`${SEL_BTN_MORE},${SEL_BTN_LESS}`).forEach((btn: Element) => {
    btn.addEventListener("click", listener)
  })
}

const toggleClass = (elem: HTMLElement, show: boolean) => {
  if (show) {
    elem.classList.add(SHOWN_CLASS)
  } else {
    elem.classList.remove(SHOWN_CLASS)
  }
}

const observeAndListen = (
  trigger: HTMLElement,
  parentRow: HTMLElement,
  rows: NodeListOf<HTMLElement>
) => {
  function listener(this: HTMLElement, event: Event) {
    event.preventDefault()
    event.stopImmediatePropagation()

    // Disconnect the observer since we know clicks are working
    observer.disconnect()

    const isShown = parentRow.classList.contains(SHOWN_CLASS)
    rows.forEach((row) => {
      toggleClass(row, !isShown)
    })
    toggleClass(parentRow, !isShown)
  }

  if (!trigger.parentNode || !parentRow) {
    return
  }

  const observer = new MutationObserver((list, obs) => {
    // Trigger removed from DOM
    if (!trigger.parentNode) {
      // Cleanup old listener
      trigger.removeEventListener("click", listener)
      // Attach new listeners
      addClickListeners(parentRow, listener)
    }
  })
  observer.observe(trigger.parentNode, { childList: true })
  addClickListeners(parentRow, listener)
}

const run = () => {
  // Get all trigger elements
  document.querySelectorAll<HTMLElement>(SEL_BTN_MORE).forEach((trigger) => {
    // Get the row containing the trigger button
    const parentRow = trigger.closest<HTMLElement>(".c-row")
    if (!parentRow) {
      return
    }

    // Find all sibling rows with the '.show-more' class
    const rows = parentRow.parentNode?.querySelectorAll<HTMLElement>(SEL_ROWS)

    if (!rows || rows.length === 0) {
      parentRow.classList.add(DISABLED_CLASS)
    } else {
      observeAndListen(trigger, parentRow, rows)
    }
  })
}

const init = () => {
  if (
    document.readyState === "complete" ||
    document.readyState === "interactive"
  ) {
    setTimeout(run, 10)
  } else {
    document.addEventListener("DOMContentLoaded", () => {
      setTimeout(run, 10)
    })
  }
}

export default {
  init,
  run,
}
